.controls{
    text-align: center;
    width: 50vmin;
    max-width: 50vmin;
    min-width: 50vmin;
    padding: 0;
    font-family: "Slabo 27px";
    display: inline-block;
    margin: 0px 0px 10px 0px;
}

.button{
    text-align: center;
    line-height: 1vmin;
    font-size: 5vmin;
    width: 6vmin;
    height: 6vmin;
    margin-top: 0.5vmin;
    margin-bottom: 0.5vmin;
    margin-left: 0.5vmin;
    margin-right: 0.5vmin;
}

.statistic{
    display: inline-block;
    text-align: left;
    font-size: 3vmin;
    font-family: "Slabo 27px";
    width: 125px;
    margin: 10px;
}

.chessGrid{
    overflow: hidden;
    display: inline-block;
    padding: 0;
    margin-top: 1vmin;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    border: 4px solid;
    border-color: black;
    border-width: 1;
    border-radius: 1vh;
    margin-bottom: 5px;
}

.row {
    height: 6vmin;
    border: none;
}

.tile {
    vertical-align: top;
    padding: 0;
    line-height: 0vmin;
    width: 6vmin;
    height: 6vmin;
    font-family: monospace;
    font-size: inherit;
    border: 0;
}

.tileDiv#true{
    font-size: 1vmin;
}

.tileDiv#false {
    font-size: 4vmin;
}

.tile:active {
    background-image: linear-gradient(rgb(0 0 0/20%) 0 0);
}

#1 {
    background-color: gainsboro;
}

#1:hover {
    background-image: linear-gradient(rgb(0 0 0/10%) 0 0);
}


#0 {
    background-color: white;
}

#0:hover {
    background-image: linear-gradient(rgb(0 0 0/10%) 0 0);
}


#3 {
    background-color: gainsboro;
    background-image: linear-gradient(rgb(255 0 0/20%) 0 0);
}

#2 {
    background-color: white;
    background-image: linear-gradient(rgb(255 0 0/20%) 0 0);
}