.pageContainer {
    display: grid;
    grid-template-columns: [left] 15% [center] 70% [right] 15%;
}

.thing {
    width: 1000px;
    margin: auto;
    text-align: center;
}

.title {
    margin: 25px;
    font-family: "Slabo 27px";
}

.subtitle {
    text-align: left;
    margin: 25px;
    font-family: "Slabo 27px";
}

.text {
    text-align: left;
    margin: 25px;
    font-family: "Slabo 27px";
    letter-spacing: 0.8px;
}

.content {
    border-left: 2px solid rgb(192, 192, 192);
    border-right: 2px solid rgb(192, 192, 192);
    text-align: center;
    grid-column-start: center;
    grid-column-end: right;
}